export default {
    data(){
        return {
            valid : false,
            userId: '',
            password :'',


        }
    },
    methods: {
        onSubmitLogin (){
            if(this.valid){
                    alert(' ');
                    this.$store.dispatch('users/login',{
                        userId: this.userId,
                        password : this.password
                    });

                }else{
                    alert(' ');
                }
                // this.$refs.form.validate()
        },
        // onLogin(){
        //     if(this.valid){
        //         this.$store.dispatch('users/login');
        //     }
        // },
        onLogout (){
            this.$store.dispatch('users/logOut');
        }
    },
    computed  :{
        me() {
            return this.$store.state.users.me;
        }
    }
    
}
