import LoginForm from '~/components/LoginForm';    //~    

export default {

    components : {
        LoginForm : LoginForm,
    },

    computed : {
        name(){
            return this.$store.state.posts.name
        }
    },
    data(){
        return {
            hashtag:'',
        }
    },
    methods: {
        onSearchHashtag (){
            this.$router.push({
                path: `/hashtag/${encodeURIComponent(this.hashtag)}`,
            });
            this.hashtag = '';
        }
    }
    
}
